import React, {useState} from 'react'

export default function Textform(props) {

const [text,setText] = useState("")

    function downloadasTextFile (filename, text) {
        var element = document.createElement('a');
        element.setAttribute('href', 'data:text/plain;charset=utf-8,' + encodeURIComponent(text));
        element.setAttribute('download', filename);
        
        element.style.display = 'none';
        document.body.appendChild(element);
        
        element.click();
        
        document.body.removeChild(element);
    }
    
    // Start file download.
    const download = () =>{
    // Generate download of phpcodertech.txt file with some content
    var filename = "phpcodertech.txt";
    
    downloadasTextFile(filename, text);
    }

    const lineCount = () => {
          if (text === "") {
              return 0;
          }
          else{
             let newLine = text.split("\n").length
             return newLine
            }
        }
        
    
    const handleOnUpClick = () => {
        // console.log("Uppercase was clicked " + text);
        let newText = text.toUpperCase()
        setText(newText)
        props.showAlert("Converted to Uppercase","success")
    }
    
    const handleOnLoClick = () => {
        // console.log("Uppercase was clicked " + text);
        let newText = text.toLowerCase()
        setText(newText)
        props.showAlert("Converted to Lowercase","success")
    }
    
    const handleOnCap = () => {
        
        var CapitalizeWords = text[0].toUpperCase();  
        for (var i = 1; i <= text.length - 1; i++) {  
            let currentCharacter,  
            previousCharacter = text[i - 1];  
            if (previousCharacter && (previousCharacter === ' ' || previousCharacter === '\n')) {  
                currentCharacter = text[i].toUpperCase();  
            } else {  
                currentCharacter = text[i];  
            }  
            CapitalizeWords = CapitalizeWords + currentCharacter;  
        }  
        
        setText(CapitalizeWords)
        props.showAlert("Converted to Capitalize","success")
    }
    
    function firstLetterUpper(theString) {
        var newString = theString.toLowerCase().replace(/(^\s*\w|[.!?\n]\s*\w)/g,function(c){return c.toUpperCase()});
        return newString;
    }
    
    const handleOnSentence = () => {
        //alert(theString);
        var newText = firstLetterUpper(text);
        //console.log("Converted: "+newString);
        setText(newText)
        props.showAlert("Converted to Sentence case","success")
    }
    
    const handleOnAlter = () => {
        var chars = text.toLowerCase().split("");
        for (var i = 0; i < chars.length; i += 2) {
            chars[i] = chars[i].toUpperCase();
        }
        setText(chars.join(""))
        props.showAlert("Converted to Altercase","success")
    }
    
    
    const handleOnInverse = () => {
        var newText = '';
        var i = 0;
        while (i < text.length) {
            var n = text.charAt(i);
            if (n === n.toUpperCase()) {
                n = n.toLowerCase();
            }
            else {
                n = n.toUpperCase();
            }
            
            i +=1; 
            newText += n;
        }
        
        setText(newText)
        props.showAlert("Converted to Inversecase","success")
    }
    
    const handleOnClearClick = () => {
        // console.log("Uppercase was clicked " + text);
        let newText = ''
        setText(newText)
        props.showAlert("Text is Cleared","warning")
    }
    
    const handleCopy = () => {
        let newText = document.getElementById("mybox")
        navigator.clipboard.writeText(newText.value)
        props.showAlert("Text is Copied to Clipboard","success")
    }
    
    const handleExtraSpace = () => {
        let newText = text.split(/[ ]+/);
        setText(newText.join(" "))
        props.showAlert("Extra space is removed from Text","success")
    }

    const handleOnChange = (event) => {
        // console.log("On Change");
        setText(event.target.value)
    }

    
    const checkColor = () => {

        if(props.mode === 'warning'){
            return 'black'
          }
          else{
            return 'white'
          }
    }

    return(
        <>
        <div className="container my-3" style={{color:checkColor()}}> 
            <h1>{props.heading}</h1>
            <div className="mb-3">
                <textarea className={`form-control text-${props.mode === 'warning' ? 'dark' : 'white'} bg-${props.mode === 'warning' ? 'light' : 'dark'}`} value={text} placeholder="Enter the text here" onChange={handleOnChange} id="mybox" rows="8" required ></textarea>
            </div>
                <button disabled={text.length === 0} className={`btn btn-${props.mode === "warning" ? 'warning':'dark'} mx-2 my-2`} onClick={handleOnSentence}>Convert to Sentence case</button>
                <button disabled={text.length === 0} className={`btn btn-${props.mode === "warning" ? 'warning':'dark'} mx-2 my-2`} onClick={handleOnUpClick}>Convert To Uppercase</button>
                <button disabled={text.length === 0} className={`btn btn-${props.mode === "warning" ? 'warning':'dark'} mx-2 my-2`} onClick={handleOnLoClick}>Convert To Lowercase</button>
                <button disabled={text.length === 0} className={`btn btn-${props.mode === "warning" ? 'warning':'dark'} mx-2 my-2`} onClick={handleOnCap}>Convert to Capitalize</button>
                <button disabled={text.length === 0} className={`btn btn-${props.mode === "warning" ? 'warning':'dark'} mx-2 my-2`} onClick={handleOnAlter}>aLtErNaTiNg cAsE</button>
                <button disabled={text.length === 0} className={`btn btn-${props.mode === "warning" ? 'warning':'dark'} mx-2 my-2`} onClick={handleOnInverse}>inVeRsE CaSe</button>
                <button disabled={text.length === 0} className={`btn btn-${props.mode === "warning" ? 'warning':'dark'} mx-2 my-2`} onClick={handleExtraSpace}>Remove Extra spaces</button>
                <button disabled={text.length === 0} className={`btn btn-${props.mode === "warning" ? 'warning':'dark'} mx-2 my-2`} onClick={handleCopy}>Copy to Clipboard</button>
                <button disabled={text.length === 0} className={`btn btn-${props.mode === "warning" ? 'warning':'dark'} mx-2 my-2`} onClick={download}>Download</button>
                <button disabled={text.length === 0} className={`btn btn-${props.mode === "warning" ? 'warning':'dark'} mx-2 my-2`} onClick={handleOnClearClick}>Clear</button>
        </div>

        <div className="container my-3" style={{color:checkColor()}}>
            <h1>Your text summary</h1>
            <p>{lineCount()} lines and {text.split(/\s+/).filter((element) => {return element.length !== 0}).length} words and {text.length} characters</p>
            <p>{0.008 * text.split(" ").length} Minutes you need to read </p>
            <h2>Preview</h2>
            <p>{text.length>0 ? text:"Nothing to Preview"}</p>
        </div>
        </>
    )
}
