import './Navbar.css';
import React from 'react'
import PropTypes from 'prop-types'
// import { Link, NavLink } from 'react-router-dom';
export default function Navbar(props) {

  return (
    <>
      <nav className={`navbar navbar-expand-lg navbar-${props.mode} bg-${props.mode}`}>
        <div className="container-fluid">
          <a className={`ms-2 navbar-brand text-${props.mode === 'warning' ? 'dark' : 'white'}`} to="/">{props.title}</a>
          <div className={`ms-auto me-2 form-check form-switch text-${props.mode === 'warning' ? 'dark' : 'light'}`}>
            <input  type="radio" id="defaultCheck1" onClick={props.toggleMode} />
            <label className="text-break text-wrap" htmlFor="defaultCheck1">Enable {props.mode === 'warning' ? 'Dark mode' : 'Light mode'}</label>
          </div>

        </div>
      </nav>

    </>
  )
}

Navbar.propTypes = { title: PropTypes.string }